<style>
  .rightPanel {
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
  }
</style>
<template>
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">责令整改</span>
      </iCol>
    </Row>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <!--<Row>-->
      <!--&lt;!&ndash;<span>单位名称：</span>&ndash;&gt;-->
      <!--<Input class="grey" placeholder="输入单位名称/建筑物名称/检查单位名称" style="width: 300px;" />-->
      <!--<Checkbox style="margin-left: 10px;">-->
      <!--<span>只看自己</span>-->
      <!--</Checkbox>-->
      <!--</Row>-->
      <Row style="margin-top: 10px;">
        <!--<span>改正期限：</span>-->
        <Select class="grey" v-model="selectedBuildingId" clearable placeholder="所有建筑" @on-change="searchDataByParam" style="width: 200px">
          <Option :value="building.buildingId" :key="building.buildingId" v-for="building in buildingList">{{ building.buildingName }}</Option>
        </Select>
        <iSelect class="grey" clearable v-model="taskType" placeholder="整改类型" @on-change="searchDataByParam" style="margin-left: 5px; width: 120px">
          <Option value="IMPROVE" :key="'IMPROVE'">立即整改</Option>
          <Option value="DEADLINE" :key="'DEADLINE'">限期整改</Option>
        </iSelect>
        <iSelect class="grey" clearable v-model="taskResult" placeholder="整改结果" @on-change="searchDataByParam" style="margin-left: 5px; width: 120px">
          <Option value="PASS" :key="'PASS'">合格</Option>
          <Option value="FAIL" :key="'FAIL'">不合格</Option>
        </iSelect>
        <DatePicker :type="'daterange'" v-model="dateRange" class="grey" placeholder="整改发布日期"
                    style="margin-left: 5px; width: 180px"></DatePicker>
        <Button @click="searchDataByParam" class="grey" style="margin-left:5px;width:80px;">搜索</Button>
      </Row>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="taskList" class="auto-height-table grey" style="width:100%">
      </Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchData" :total="pageTotal" :pageSize="pageSize" class="default" :current="1"></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import common from '../../../libs/common';
  import util from '../../../libs/util';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data() {
      return {
        newData: {
          name: undefined,
        },
        selectedBuildingId: undefined,
        taskType: undefined,
        taskResult: undefined,
        dateRange: [],
        taskList: [],
        common: undefined,
        searchBuildingId: undefined,
        pageTotal: undefined,
        buildingList: [],
        pageSize: 20,
        modal: false,
        searchParam: '',
        title: '创建',
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width: '50'
          },
          {
            title: '建筑物',
            align: 'center',
            key: 'targetBuildingName'
          },
          {
            title: '整改类型',
            key: 'taskName',
            width: '180',
            align: 'center'
          },
          {
            title: '当前状态',
            align: 'center',
            width: '80',
            render: (h, params) => {
              if(params.row.status === 'OPEN') {
                return h('span', '未下发');
              } else if(params.row.status === 'EXECUTING') {
                return h('span', '整改中');
              } else if(params.row.status === 'TOREVIEW') {
                return h('span', '待复查');
              } else if(params.row.status === 'DONE') {
                if(params.row.result === 'PASS') {
                  return h('span', '合格');
                } else if(params.row.result === 'FAIL') {
                  return h('span', '不合格');
                } else {
                  return h('span', '未知');
                }
              } else {
                return h('span', '未知');
              }
            }
          },
          {
            title: '计划复查日期',
            align: 'center',
            width: '120',
            render: (h, params) => {
              return h('span', this.common.revertMTime(params.row.planEndDt));
            }
          },
          {
            title: '检查人',
            align: 'center',
            width: '100',
            render: (h, params) => {
              if (params.row.creatUser === undefined || params.row.creatUser === null) {
                return h('span', '-');
              }
              return h('span', params.row.creatUser.name);
            }
          },
          {
            title: '检查单位',
            align: 'center',
            key: 'createrOrgName'
          },
          {
            title: '操作',
            align: 'center',
            width: '80',
            render: (h, params) => {
              return h('div', [
                h('i-button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#a9b2fd'
                  },
                  on: {
                    click: () => {
                      this.showDetail(params.row.taskId);
                    }
                  }
                }, '查看')
              ]);
            }
          }
        ],
        formValidate: {
          name: [
            {required: true, message: '姓名不能为空'}
          ]
        }
      };
    },
    methods: {
      initData: function () {
        // 初始化数据
        this.searchData(1);
        // 查询建筑物列表，做检索
        this.searchBuildingList();
      },
      showDetail: function (taskId) {
        this.$router.push({name: 'orderRectDetail', query: {taskId: taskId}});
      },
      createData: function (formRef) {
        this.$refs[formRef].validate((valid) => {
          if (valid) {
            this.$Message.success('保存成功!');
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      searchBuildingList: function () {
        // buildingList
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', localStorage.current_org_id)).then((response) => {
          if (!response.body.meta.success) {
            this.$Message.error("获取建筑物状态失败！");
            return;
          }
          this.buildingList = response.body.data;
        }, (error) => {
          this.$Message.error("获取建筑物状态失败！");
        });
      },
      searchData: function (pageNum) {
        // 分页查询
        let url = server.api.routine.record.list.replace('{orgId}', localStorage.current_org_id) + '?orgId=' + localStorage.current_org_id + '&pageSize=' + this.pageSize + '&pageNum=' + pageNum;
        if (this.selectedBuildingId !== undefined) {
          url += '&buildingId=' + this.selectedBuildingId;
        }
        if(this.taskType !== undefined && this.taskType !== null && this.taskType.length !== 0) {
          url += '&taskType=' + this.taskType;
        } else {
          // 查询所有的监督检查记录
          url += '&taskType=IMPROVE,DEADLINE';
        }
        if(this.taskResult !== undefined && this.taskResult !== null && this.taskResult.length !== 0) {
          url += '&taskResult=' + this.taskResult;
        }
        let range = [];
        if (this.dateRange !== undefined && this.dateRange !== null && this.dateRange.length !== 0
            && this.dateRange[0] !== null && this.dateRange[0].length !== 0) {
          range.push(util.dateFormat(this.dateRange[0], 'yyyy-MM-dd HH:mm:ss'));
          range.push(this.dateRange[1]);
          range[1].setDate(range[1].getDate() + 1);
          range[1].setSeconds(range[1].getSeconds() - 1);
          range[1] = util.dateFormat(range[1], 'yyyy-MM-dd HH:mm:ss');
          url += '&startTime=' + range[0];
          url += '&endTime=' + range[1];
        }
        this.$http.get(url).then((response) => {
          this.pageTotal = response.body.data.total;
          this.taskList = response.body.data.rows;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }, (error) => {
          this.$Message.error("获取记录失败！");
        });
      },
      searchDataByParam: function () {
        // 条件查询
        this.searchData(1);
      },
    },
    mounted() {
      this.common = common;
      this.initData();
    },
    beforeRouteLeave(to, from, next){
      $(".auto-height-table .ivu-table-body").getNiceScroll().hide();
      next();
    }
  }
</script>



